body
  background: var(--background_color)

.brown
  color: var(--letter_accent_color)

.tittle
  font-size: 75px
  font-weight: bolder

#tittle1
  position: absolute
  left: 13%
  top: 20%

#tittle2
  position: absolute
  left: 13%
  top: 27%

#wave
  position: absolute
  left: 42%
  top: 20%
  width: 58%
  height: 80%

#salad
  position: absolute
  left: 62%
  top: 20%
  width: 30%
  height: auto
  pointer-events: none
  animation: rotation 25s infinite linear

#line
  position: absolute
  border-radius: 5px
  left: 13%
  top: 37%
  height: 150px
  width: 9px
  background-color: var(--second_letter_accent_color)

#description
  position: absolute
  font-size: 17px
  left: 14.2%
  top: 38.5%
  width: 13%
  height: auto

#ready-btn
  position: absolute
  left: 40.5%
  top: 74%

@keyframes rotation
  from
    transform: rotate(0deg)

  to
    transform: rotate(360deg)
